import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
// import Home from '../views/Home.vue'
const Home = () => import("../views/Home.vue");
const Welcome = () => import("../views/Welcome.vue");
const User = () => import("../views/User.vue");
const DeviceUser = () => import("../views/DeviceUser.vue");
const Password = () => import("../views/Password.vue");
const Log = () => import("../views/Log.vue");
const Access = () => import("../views/Access.vue");
const Calculate = () => import("../views/Calculate.vue");
const ApplicationUser = () => import("../views/ApplicationUser.vue");
const Manager = () => import("../views/Manager.vue");
const Status = () => import("../views/Status.vue");
const Mainkey = () => import("../views/Mainkey.vue");
const Material = () => import("../views/Material.vue");
const KeySource = () => import("../views/KeySource.vue");
const Version = () => import("../views/Version.vue");
const Encrypt = () => import("../views/Encrypt.vue");
const KeyInfo = () => import("../views/KeyInfo.vue");
const Sversion = () => import("../views/Sversion.vue");
const ManagerCard = () => import("../views/ManagerCard.vue");
const DeviceAuth = () => import("../views/DeviceAuth.vue");
const Resources = () => import("../views/Resources.vue");
const ResourceDetail = () => import("../views/ResourceDetail.vue");
const Auth = () => import("../views/Auth.vue");
const Strategy = () => import("../views/Strategy.vue");
const Group = () => import("../views/Group.vue");
const GroupDetail = () => import("../views/GroupDetail.vue");
const GroupAppDetail = () => import("../views/GroupAppDetail.vue");
const Role = () => import("../views/Role.vue");
const RoleDetail = () => import("../views/RoleDetail.vue");
const RoleAppDetail = () => import("../views/RoleAppDetail.vue");
const Pwsp = () => import("../views/Pwsp.vue");
const Total = () => import("../views/Total.vue");
const Alert = () => import("../views/Alert.vue");
const Qems = () => import("../views/Qems.vue");
const RoleAuth = () => import("../views/RoleAuth.vue");
const GroupAuth = () => import("../views/GroupAuth.vue");
const ApplicationAuth = () => import("../views/ApplicationAuth.vue");
const NoFound = () => import('../views/NoFound.vue');
import Cookies from 'js-cookie'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/login'
    // component: Login
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },

  {
    path: '/password',
    name: 'Password',
    component: Password
  },
  {
    path: '*',
    name: 'NoFound',
    component: NoFound
  },
  {
    path: '/home',
    component: Home,
    redirect: '/welcome',
    children: [
      {path: '/welcome', component: Welcome},
      {path: '/user', component: User},
      {path: '/deviceUser', component: DeviceUser},
      {path: '/calculate', component: Calculate},
      {path: '/applicationUser', component: ApplicationUser},
      {path: '/log', component: Log},
      {path: '/manager', component: Manager},
      {path: '/status', component: Status},
      {path: '/access', component: Access},
      {path: '/keySource', component: KeySource},
      {path:'/mainkey',component:Mainkey},
      {path:'/encrypt',component:Encrypt},
      {path: '/material', component: Material},
      {path: '/version', component: Version},
      {path: '/keyInfo', component: KeyInfo},
      {path: '/sversion', component: Sversion},
      {path: '/cardList', component: ManagerCard},
      {path: '/deviceAuth', component: DeviceAuth},
      {path: '/resources', component: Resources},
      {path: '/resourceDetail', component: ResourceDetail},
      {path: '/auth', component: Auth},
      {path: '/total', component: Total},
      {path: '/alert', component: Alert},
      {path: '/strategy', component: Strategy},
      {path: '/group', component: Group},
      {path: '/groupDetail', component: GroupDetail},
      {path: '/groupAppDetail', component: GroupAppDetail},
      {path: '/role', component: Role},
      {path: '/roleDetail', component: RoleDetail},
      {path: '/roleAppDetail', component: RoleAppDetail},
      {path: '/pwsp', component: Pwsp},
      {path: '/qems', component: Qems},
      {path: '/roleAuth', component: RoleAuth},
      {path: '/groupAuth', component: GroupAuth},
      {path: '/applicationAuth', component: ApplicationAuth},

    ]
  }
];

const router = new VueRouter({
  routes,
});

router.beforeEach((to, from, next) => {
//to将要访问的路径，from代表从哪一个路径跳转而来
  if (to.path === '/login') return next();
  const tokenStr = window.sessionStorage.getItem('token');
  if (!tokenStr) {
    window.sessionStorage.clear();
    return next('/login');
  }
  next()
});
export default router
